.header-link {
 position: relative;
 color: inherit;
 font-size: 1.3em;
 margin: auto 1vw;
 text-transform: capitalize;
 transition: color 0.6s;
}

.header-link:after {
 content: "";
 display: block;
 position: absolute;
 width: 0;
 left: 25%;
 bottom: 0;
 /* bottom: -3px; */
 /* border-bottom: 4px solid transparent; */
 border-bottom: 4px solid var(--color-2);
 border-radius: 4px 4px 0 0;
 transition: all .5s;
}

.header-link:hover:after,
.header-link.active:after {
 /* border-color: var(--color-2); */
 width: 50%;
}

.link {
 /* color: var(--text-secondary); */
 color: inherit;
 text-decoration: none;
 /* line-height: 2; */
 opacity: .8;
 transition: color 0.5s;
}

.sidebar-link {
 position: relative;
 font-size: 1.5rem;
 color: inherit;
 text-decoration: none;
 /* line-height: 3; */
 /* margin: 0.5em 1em; */
}

.sidebar-link:before {
 content: "";
 display: block;
 position: absolute;
 height: 0;
 left: -1rem;
 bottom: 0;
 border-left: 4px solid var(--color-2);
 /* border-radius: 4px 0 0 4px; */
 border-radius: 0 4px 4px 0;
 /* transition: all .5s; */
 transition: height .5s;
}

.sidebar-link:hover:before,
.sidebar-link.active:before {
 height: 100%;
}

.link:hover,
.link:focus,
.nav-link:hover,
.nav-link:focus,
.nav-link.active,
.sidebar-link.active,
.sidebar-link:hover,
.icon:hover {
 /* color: var(--color-4); */
 color: var(--color-2);
 text-decoration: none;
 opacity: 1;
 /* transition: color 0.4s; */
}


