}

.btn-main {
 background-color: var(--color-2);
 background-image: linear-gradient(180deg, var(--color-2-tint) 0%, var(--color-2) 100%);
 color: var(--color-1-tint);
 border: 1px solid var(--color-2);
}

.btn-gold {
 background-color: var(--color-4);
 background-image: linear-gradient(180deg, var(--color-4-tint) 0%, var(--color-4) 100%);
