 border: 1px solid var(--color-4);
}

.btn-white {
 background-color: var(--color-1);
 background-image: linear-gradient(180deg, var(--color-1-tint) 0%, var(--color-1) 100%);
 color: var(--text-primary);
 border: 1px solid var(--color-1);
}

